<template>
  <article class="item-cell">
    <a href="#/items/{{ item.key }}">
      <h5 class="item_title">{{ item.title }}</h5>
      <div class="full-width"><img :src="item.square_cover_photo_url" /></div>
      <p><span class="price">{{ item.price / 100 | currency "¥ " 2 }}</span></p>
    </a>
  </article>
</template>

<script>
export default {
  name: 'Item',

  props: {
    item: Object
  }, 
}
</script>
<style>
  h5{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  /* add separators betwen item-cells */
  .item-cell ~ .item-cell {
    border-left: 1px solid #ddd;
  }
</style>



